/* @import '~antd/dist/antd.css'; */

*{
    padding:0;
    margin: 0;
}
.header{
    height:50px;
    background-color: #444;
}
.header header{
    width:1200px;
    margin:0 auto;
    line-height:50px;
    background-color: #444;
}
.header header a{
    margin:0 20px;
    color:#fff;
}
.header img{
    width:120px;
}



/*content*/
.main{
    background: #e1e1e1;
    width:100%;
}
.content{  
    width:1200px;
    margin:0 auto;
}
.left{
    background: white;
    margin-top: 30px;
    width:870px;
    float:left;
    /* background-color: white; */
}
.right{
    margin-top: 30px;
    height:570px;
    float:right;
    background-color: palevioletred;
    width: 290px;
    font-size: 14px;
    /* margin-bottom: 20px; */
}

/* footer */
.fix-bottom{
    background-color: white;
    height:190px;
    width: 100%;
    font-size: 14px;
    word-break: break-word;
}
.fix-bottom2{
    width:1200px;
    margin:30px auto 0;
}
.dark{
    color: #666;
    text-decoration: none;
}
.col_fade {
    color: #ababab;
}
.col_fade p {
    line-height: 20px;
    vertical-align: bottom;
}


/* Signin */
.head{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    font-size: 10px;
}
.inner{
    height:265px;
}
.inner div:first-child{
    margin-top: 30px;
}
.inner div{
    width:250px;
    height:30px;
    margin-left: 100px;
}
.inner div p{
    float: left;
}
.inner div input{
    float:right;
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle
}
.onhome{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin-left: 180px;
    margin-top: 20px;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}